* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Helvetica", sans-serif;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #4d6cfa;
}
.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  border-radius: 30px;
  background: #e0e0e0;
  box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
}
h1 {
  margin-bottom: 1rem;
}
input[type="text"] {
  height: 1.2rem;
  width: 100%;
  padding: 0.7rem 0.5rem;
  margin-bottom: 1rem;
  border-radius: 5px;
  background-color: #fff;
  border: none;
  border-bottom: 2px #595457;
  outline: none;
}
button {
  width: 80%;
  padding: 0.5rem;
  font-size: 14px;
  border-radius: 10px;
  color: #fff;
  background-color: #4d6cfa;
  outline: none;
  border: none;
  transition: transform 0.3s ease-in-out;
}
button:active {
  outline: none;
}
button:hover {
  transform: scale(1.1);
}
